module.exports.toNpNumber = (number) => {
    if (!number) return null;
    let dict = {
        0: 'реж',
        1: 'рез',
        2: 'реи',
        3: 'рей',
        4: 'рек',
        5: 'рел',
        6: 'рем',
        7: 'рен',
        8: 'рео',
        9: 'реп',
    };
    let strNum = number + '';
    return strNum.split('').map(n => dict[n] || n ).join('');
}
